import React from "react";
import "./index.scss";
import { Link, useHistory } from "react-router-dom";

// import { FaTimes } from "react-icons/fa";
import {
  CContainer,
  CForm,
  CFormGroup,
  CLabel,
  CInput,
  CButton,
} from "@coreui/react";

const ClientLogin = (props) => {
  const history = useHistory();

  const onSubmit = () => {
    localStorage.setItem("isLogin", true);
    history.push("/dashboard");
  };

  return (
    <div className="loginPage">
      {/* <Link to="/" className="btnClose">
        <FaTimes />
      </Link> */}

      <CContainer>
        <div className="content">
          <CForm action="" method="post">
            <h1>Log In</h1>
            <h4>
              New to this site?<Link to="/register">Sign Up</Link>
            </h4>

            <CFormGroup>
              <CLabel htmlFor="nf-email">Email</CLabel>
              <CInput
                type="email"
                id="nf-email"
                name="nf-email"
                autoComplete="email"
              />
            </CFormGroup>

            <CFormGroup>
              <CLabel htmlFor="nf-email">Password</CLabel>
              <CInput type="password" id="nf-password" name="nf-password" />
            </CFormGroup>

            <Link className="forgotPassword" to="/forgot-password">
              Forgot password?
            </Link>

            <div className="actions">
              <CButton color="primary" classID="btnLogin" onClick={onSubmit}>
                Log In
              </CButton>
            </div>
          </CForm>
        </div>
      </CContainer>
    </div>
  );
};

export default ClientLogin;
